import React, { useState, useEffect } from "react";
import axios from "axios";
import "./home.css";
import "./Pagination.css";
import ReactPaginate from "react-paginate";
import { connect } from "react-redux";

const Home = (props) => {
  const [data, setData] = useState([]);
  const [orgtableData, setOrgTableData] = useState([]);
  const [offset, setOffset] = useState(0);
  const [perPage] = useState(9);
  const [currentPage, setCurrentPage] = useState(0);
  const [pageCount, setPageCount] = useState(0);
  const [serial, setSerial] = useState(1);

  const [favData, setFavData] = useState([]);
  const [display, setDisplay] = useState(false);
  const [item, setItem] = useState("");

  //   https://api.punkapi.com/v2/beers?beer_name=Pilsen%20Lager
  const receivedData = () => {
    axios.get(`https://api.punkapi.com/v2/beers`).then((res) => {
      console.log(res.data);
      //   setData(res.data);

      const data = res.data;
      const slice = data.slice(offset, offset + perPage);
      setOrgTableData(res.data);
      setPageCount(Math.ceil(data.length / perPage));
      setData(slice);
    });
  };

  useEffect(() => {
    receivedData();
  }, []);

  const handlePageClick = (e) => {
    const selectedPage = e.selected;
    const offset = selectedPage * perPage;
    setSerial(offset + 1);
    setOffset(offset);
    setCurrentPage(selectedPage);
  };

  useEffect(() => {
    loadMoreData();
  }, [offset, currentPage]);

  const loadMoreData = () => {
    const data = orgtableData;
    const slice = data.slice(offset, offset + perPage);
    setPageCount(Math.ceil(data.length / perPage));
    setData(slice);
  };

  const searchHandler = (key) => {
    axios
      .get(`https://api.punkapi.com/v2/beers?beer_name=${key}`)
      .then(function (response) {
        if (response.data) {
          setData(response.data);
        }
      });
  };

  const handleCheckbox = (e, s) => {
    const checkedBoxes = [...favData];

    if (e.target.checked) {
      checkedBoxes.push(s);
    } else {
      const index = checkedBoxes.findIndex((ch) => ch.id === s.id);
      checkedBoxes.splice(index, 1);
    }
    // setDisplay(!display);

    setFavData(checkedBoxes);
    {
      props.submit1(checkedBoxes);
    }
  };

  const deleteHandler = (id) => {
    // console.log(id);
    const item = [];
    item.push(id);
    item.forEach(function (p) {
      setData(data.filter((prd) => prd.id !== p));
    });
  };

  const editHandler = (name) => {
    // console.log("edit", name);
    setDisplay(!display);
    setItem(name);
  };

  const changeHandler = (e) => {
    console.log("changeHandler :", e);
    setItem(e);
  };

  const updateHandler = (item) => {
    console.log("updateHandler :");
    setData(item)
  };

  return (
    <div>
      <div className="card">
        <div className="card-header">
          <input
            type="text"
            style={{ width: "240px" }}
            placeholder="search"
            onChange={(event) => searchHandler(event.target.value)}
          />
          {display && (
            <div>
              <label>Edit :</label>
              <input
                type="text"
                defaultValue={item}
                onChange={(event) => changeHandler(event.target.value)}
              />
              <button onClick={() => updateHandler(item)}>update</button>
            </div>
          )}
        </div>

        <div className="card-body">
          <div className="row">
            {data.map((p, i) => (
              <div className="col-sm-4 card_head" key={i}>
                <div className="card card_data">
                  <div className="img_body">
                    <input
                      type="checkbox"
                      name="sensorId"
                      checked={favData.find((ch) => ch.id === p.id)}
                      onChange={(e) => handleCheckbox(e, p)}
                    />

                    <img
                      className="card-img-top"
                      src={p.image_url}
                      alt="Card image"
                    />
                  </div>

                  <div
                    className="card-body"
                    style={{
                      backgroundColor:
                        favData.find((ch) => ch.id === p.id) && "skyblue",
                    }}
                  >
                    <button onClick={() => deleteHandler(p.id)}>delete</button>
                    <h4
                      className="card-title"
                      onClick={() => editHandler(p.name)}
                    >
                      {p.name}
                    </h4>
                    <p className="card-text">{p.description}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>

          <ReactPaginate
            previousLabel={"prev"}
            nextLabel={"next"}
            breakLabel={"..."}
            pageCount={pageCount}
            marginPagesDisplayed={1}
            pageRangeDisplayed={3}
            onPageChange={handlePageClick}
            containerClassName={"pagination"}
            subContainerClassName={"pages pagination"}
            activeClassName={"active"}
            breakClassName={"page-item"}
            breakLinkClassName={"page-link"}
            pageClassName={"page-item"}
            pageLinkClassName={"page-link"}
            previousClassName={"page-item"}
            previousLinkClassName={"page-link"}
            nextClassName={"page-item"}
            nextLinkClassName={"page-link"}
          />
        </div>
      </div>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    submit1: (data) => {
      dispatch({
        type: "SUBMIT",
        payload: {
          data: data,
        },
      });
    },
  };
};

export default connect(null, mapDispatchToProps)(Home);
